@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
.level, .score {
    position: absolute;
    top: 20px;
    left: 20px;
    width: 20%;
}

.title, .lives {
    position: absolute;
    width: 20%;
    text-align: center;
    top: 20px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    padding: 5px;
}

.title {
    border-bottom: 3px solid white;
    border-top: 3px solid white;
}

.lives {
    top: 10px;
    height: 50px;
}

.life {
    background-image: url(/number-munchers/static/media/muncher.f6020cca.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 50px;
    display: inline-block;
    margin-right: 10px;
    width: 50px;
}

.score > span:nth-child(1) {
    top: 8px;
    position: relative;
}

.points {
    border: 3px solid white;
    padding: 5px;
    width: 50%;
    float: right;
}
.square {
    border: 1px solid #ff4cf7;
    float: left;
    height: 100%;
    position: relative;
    margin-right: -2px;
    margin-top: -2px;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 16.666%;
}
.info {
    width: 100%;
    height: 75px;
    position: relative;
}

.board {
    border: 4px double #ff4cf7;
    position: relative;
    width: 95%;
    height: calc(100% - 160px);
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.board-row {
    height: 20%;
    width: 100%;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}
.muncher {
    position: absolute;
    background-image: url(/number-munchers/static/media/muncher.f6020cca.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    z-index: -1;
    top: 0;
    height: 20%;
    padding: 0;
    text-align: center;
    width: 16.666%;
}
.notification {
    position: absolute;
    background: #000574;
    width: calc(100% - 1px);
    height: calc(20% - 2px);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index:1;
    left:1px;
    top: 40%;
}
html, body, #root, .full {
    margin: 0;
    height: 100%;
    background: #000574;
    color: white;
}

body {
    margin: 0;
    font-family: 'Press Start 2P', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 24px;
    font-weight: bold;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
